import React from 'react'

import Loading from '../../components/Loading'

import imgQuake from '../../assets/images/bg0-q3.jpg'
import xx from '../../assets/images/SeasonalRank.png'

import { makeStyles } from '@material-ui/core/styles'
import { Typography, Divider, Grid } from '@material-ui/core'

// CSS Custom
const useStyles = makeStyles(theme => ({
   rootGameView: {
     minHeight: '42.4vh',
     fontFamily: 'monospace',
     borderRadius: "2px",
     boxShadow: "2px 2px 2px #3db13c57",
     textAlign: 'center',
     color: "#d3d3e6",
     backgroundImage: `url('${imgQuake}')`,
     backgroundRepeat: 'no-repeat',
     backgroundAttachment: 'fixed',
     backgroundPosition: 'center center',
     padding: '0 30px 30px 30px',
   },
   tableRanking: {
      fontFamily: 'monospace',
      fontSize: '20px',
      width: '100%',
   },
   inf0data: {
      fontFamily: 'monospace',
      textAlign: 'center',
      width: '100%',
   },
   btnRefresh: {
      padding: '5px',
      border: '1px solid #333',
      cursor: 'pointer',
   },
}))


export default (props) => {

   // pega o CSS customizado
   const classes = useStyles();

   return (
      <div>
         <Typography variant="h6" component="h4" display="block">Quake Arena 3 - Ranking Geral de Kills por Jogador</Typography>
         <Divider />
         <br />

         { props.loading
         ?  <Loading />
         : <>
            {
               props.rank.length > 0

               ?
               
                  <div className={classes.rootGameView}>

                        <img src={xx} alt="Medalha" />

                        <table className={classes.tableRanking}>
                           <thead>
                              <tr style={{fontWeight: 'bold', textTransform: 'uppercase', fontFamily: 'Helvetica', fontSize: '20px', color: '#d66565' }}>
                                 <th>Rank</th>
                                 <th>Player</th>
                                 <th>Total Kills</th>
                              </tr>
                           </thead>
                           <br />
                           <tbody>
                              {
                                 props.rank.map( ([player, value], index) => (
                                    <tr key={player}>
                                       <td>{index+1}°</td>
                                       <td>{player}</td>
                                       <td>{value}</td>
                                    </tr>
                                 ))
                              }
                           </tbody>
                        </table>               
                     </div>
                     
                  :

                     <div className={classes.inf0data}>
                        <div>Nenhum registro encontrado.</div>
                        <br />
                        <span onClick={props.getGameLog} className={classes.btnRefresh}>atualizar</span>
                     </div>
               }
               </>
            }
      </div>
   )
}